  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: 'circular-std-black-italic';
  src: url('./font/CircularStd-BlackItalic.woff') format('woff');
  font-weight: 900;
  font-style: italic;
}

@font-face {
  font-family: 'circular-std-bold';
