  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'circular-std-bold-italic';
  src: url('./font/CircularStd-BoldItalic.woff') format('woff');
  font-weight: bold;
  font-style: italic;
}

@font-face {
  font-family: 'circular-std-medium';
