@import url('https://fonts.googleapis.com/css?family=Zilla+Slab');

/* Import AOS */

@import url('https://unpkg.com/aos@2.3.1/dist/aos.css');

/* Global Styles */

.body {
width: 784px;
margin-left: auto;
margin-right: auto;
}

/* Header Styles */

.header {
width: 100%;
margin-top: 115px;
margin-bottom: 76px;
font-family: circular-std-medium, Arial, sans-serif;
}

.header .navigation {
width: 100%;
display: flex;
}

.header .navigation div a {
color: #BBBBBB;
text-decoration: none;
transition: color 0.2s;
}

.header .navigation div a:hover {
color: black !important;
}

.header .navigation .active {
color: black !important;
}

.header .overlay {
visibility: hidden;
position: fixed;
z-index: 100;
top: 0;
left: 0;
width: 100vw;
height: 100vh;
background-color: white;
opacity: 0;
transition: opacity 0.5s;
display: flex;
flex-wrap: wrap;
justify-content: center;
align-items: center;
text-align: center;
}

.header .overlay .submenus {
height: 180px;
margin-top: auto;
margin-bottom: auto;
display: flex;
flex-wrap: wrap;
}

.header .overlay .submenus .work-mobile,
.header .overlay .updates-mobile,
.header .overlay .about-mobile {
width: 100%;
font-size: 24px;
}

.show {
visibility: visible !important;
opacity: 1 !important;
}
.hide {
visibility: hidden !important;
}

.header .navigation-sub {
width: 276px;
font-size: 22px;
display: flex;
}

.header .navigation .veronica {
font-size: 26px;
width: calc(100% - 276px);
line-height: 36px;
}

.header .navigation .navigation-sub .work, .updates {
padding-right: 40px;
}

.header .designer {
font-size: 26px;
line-height: 36px;
}

.header .speciality {
font-size: 20px;
color: #02CF5D;
line-height: 36px;
}


/* Project Header Styles */

.header-project {
width: 784px;
height: 40px;
margin-left: auto;
margin-right: auto;
padding-top: 115px;
font-family: circular-std-medium, Arial, sans-serif;
z-index: 10;
}

.header-project .navigation {
width: 100%;
display: flex;
}

.header-project .navigation .navigation-sub div a,
.header-project .navigation .veronica a {
text-decoration: none;
transition: color 0.2s;
text-shadow: 0px 0px 4px rgba(0,0,0,0.5);
}

.header-project .navigation .navigation-sub div a:hover,
.header-project .navigation .veronica a:hover {
color: white !important;
}

.header-project .navigation .navigation-sub div a:visited,
.header-project .navigation .veronica a:visited {
color: inherit;
}

.header-project .overlay {
visibility: hidden;
position: fixed;
z-index: 100;
top: 0;
left: 0;
width: 100vw;
height: 100vh;
background-color: white;
opacity: 0;
transition: opacity 0.5s;
display: flex;
flex-wrap: wrap;
justify-content: center;
align-items: center;
text-align: center;
}

.header-project .overlay .submenus {
height: 180px;
margin-top: auto;
margin-bottom: auto;
display: flex;
flex-wrap: wrap;
}

.header-project .overlay .submenus .work-mobile,
.header-project .overlay .submenus .updates-mobile,
.header-project .overlay .submenus .about-mobile {
width: 100%;
font-size: 24px;
}

.header-project .overlay .submenus div a {
color: #BBBBBB !important;
text-decoration: none;
}

.header-project .overlay .submenus div a:hover {
color: black !important;
}

.show {
visibility: visible !important;
opacity: 1 !important;
}
.hide {
visibility: hidden !important;
}

.header-project .navigation-sub {
width: 276px;
font-size: 22px;
display: flex;
}

.header-project .navigation .veronica {
font-size: 26px;
width: calc(100% - 276px);
line-height: 36px;
color: white;
}

.header-project .navigation .navigation-sub .work,
.header-project .navigation .navigation-sub .updates {
padding-right: 40px;
}


/* Footer */
