  }

  .memory .pc-designs .project {
    width: calc(100% + 80px);
    left: 50%;
    margin-left: calc((100vw + 80px) / -2);
    z-index: 1;
    margin-top: 30px;
  }

  .memory .mobile-designs .image {
    width: calc(100% + 100px);
    left: 50%;
    margin-left: calc((100vw + 100px) / -2);
